
.myRelease {
  padding: 5px 10px;
}
.myRelease li {
  border: solid 1px #e3e2e1;
  border-radius: 5px;
  padding: 5px 5px;
  margin: 8px 0;
}
.myRelease li div {
  padding: 5px 0;
  font-size: 12px;
}
.myRelease li div .colRed {
  color: #ff2a0a;
}
.myRelease li div .active {
  color: #32ff05;
}
.myRelease li .m-title {
  color: #ff2a0a;
  line-height: 1.4;
  padding: 5px 2px;
  word-break: break-all;
}


