
.myIncome {
  padding: 15px 10px;
}
.myIncome ul li {
  height: 70px;
  background: #ebebeb;
  border-radius: 4px;
  padding: 0 8px;
  margin: 7px 0;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  -ms-flex-pack: distribute;
      justify-content: space-around;
}
.myIncome ul li img {
  height: 40px;
  width: 40px;
}
.myIncome ul li .in-info {
  width: 79%;
}
.myIncome ul li .in-info .in-name {
  color: #333;
  font-size: 14px;
  padding: 3px 0;
}
.myIncome ul li .in-info .in-time {
