  bottom: 0;
  left: 0;
  z-index: 1200;
  background: #fff;
  border-top: solid 1px #ebebeb;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: distribute;
      justify-content: space-around;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
}
.myIncome .subIncome input {
  width: 40%;
  outline: none;
  background: #fff;
  border: solid 1px #cc1438;
  color: #cc1438;
  border-radius: 5px;
  font-size: 14px;
  padding: 8px 8px;
}
.myIncome .subIncome .buyActive {
  background: #cc1438;
  color: #ebebeb;
}
.myIncome .subrelease {
  width: auto;
  padding: 0 15px;
}
.myIncome .subrelease span {
  font-size: 12px;
  line-height: 1.4;
  padding-right: 10px;
}
.myIncome .subrelease input {
  width: 30%;
